:root {
    --brand-red: #814d49;
    --brand-hover-red: #6a3f3c;
    --brand-blue: #3C3B6E;
    --brand-hover-blue: #2A2A4D;
    --brand-white: #FFFFFF;
    --brand-gray: #F5F5F5;
    --brand-dark: #1A1A1A;
}

.event-booking-container {
    max-width: 800px;
    margin: 0 auto;
    padding: clamp(3rem, 8vh, 5rem) clamp(1rem, 5vw, 1.25rem);
}

.form-header {
    text-align: center;
    margin-bottom: 40px;
}

.form-header h2 {
    color: var(--brand-red);
    font-size: 2.5rem;
    margin-bottom: 15px;
}

.intro-text {
    color: var(--brand-dark);
    font-size: 1.1rem;
    line-height: 1.6;
}

.form-section {
    background: white;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 30px;
}

.form-section h3 {
    color: var(--brand-red);
    font-size: 1.5rem;
    margin-bottom: 25px;
    padding-bottom: 10px;
    border-bottom: 2px solid var(--brand-red);
}

.form-group {
    margin-bottom: 25px;
}

.form-group label {
    display: block;
    color: var(--brand-dark);
    font-weight: 600;
    margin-bottom: 8px;
}

.form-group small {
    display: block;
    color: #666;
    font-size: 0.9rem;
    margin-bottom: 8px;
    font-style: italic;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="date"],
.form-group input[type="time"],
.form-group select,
.form-group textarea {
    width: 100%;
    padding: 12px;
    border: 2px solid #ddd;
    border-radius: 6px;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
    border-color: var(--brand-red);
    outline: none;
    box-shadow: 0 0 0 3px rgba(129, 77, 73, 0.1);
}

.time-range {
    background: var(--brand-gray);
    padding: 20px;
    border-radius: 6px;
    margin-top: 15px;
}

.time-inputs {
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
}

/* Column layout for event time section */
.time-range .time-inputs {
    flex-direction: column;
    gap: 20px;
}

/* Try to encourage dropdowns to open downward (best effort, not guaranteed) */
.time-input select {
    /* These properties help, but do not guarantee, downward opening */
    position: relative;
    z-index: 1;
    /* Add enough margin below to encourage space */
    margin-bottom: 24px;
    /* Remove margin for last child */
}
.time-input:last-child select {
    margin-bottom: 0;
}

.time-input {
    flex: 1;
    min-width: 200px;
    display: flex;
    align-items: center;
    gap: 15px;
}

.time-input label {
    min-width: 70px;
    margin-bottom: 0;
    font-weight: 600;
}

.time-input select {
    flex: 1;
    padding: 10px;
    border: 2px solid #ddd;
    border-radius: 6px;
    font-size: 1rem;
    background-color: white;
    cursor: pointer;
    appearance: none;
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: right 10px center;
    background-size: 1em;
    padding-right: 40px;
}

.time-input select:focus {
    border-color: var(--brand-red);
    outline: none;
    box-shadow: 0 0 0 3px rgba(129, 77, 73, 0.1);
}

.time-input select:hover {
    border-color: var(--brand-red);
}

.time-input select.disabled-select:disabled {
    background-color: #f0f0f0;
    color: #aaa;
    border-color: #ddd;
    cursor: not-allowed;
    opacity: 1;
}

.radio-group {
    display: flex;
    gap: 30px;
    margin-top: 10px;
}

.radio-group label {
    display: flex;
    align-items: center;
    gap: 8px;
    font-weight: normal;
    cursor: pointer;
}

.radio-group input[type="radio"] {
    width: 18px;
    height: 18px;
    accent-color: var(--brand-red);
}

.helper-text {
    color: #666;
    font-size: 0.9rem;
    margin: 8px 0;
}

.minimum-sales-note {
    margin: 10px 0;
    text-align: left;
    color: var(--brand-dark);
}

.minimum-sales-note p {
    margin: 0;
}

.submit-button {
    width: 100%;
    padding: 15px;
    background: var(--brand-red);
    color: white;
    border: none;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.submit-button:hover {
    background: var(--brand-hover-red);
}

.status-message {
    padding: 15px;
    border-radius: 6px;
    margin-bottom: 20px;
    text-align: center;
}

.status-message.success {
    background: #d4edda;
    color: #155724;
    border: 1px solid #c3e6cb;
}

.status-message.error {
    background: #f8d7da;
    color: #721c24;
    border: 1px solid #f5c6cb;
}

.advertising-section {
    background: var(--brand-gray);
    padding: 20px;
    border-radius: 6px;
    margin: 25px 0;
}

.advertising-section .form-group {
    margin-bottom: 0;
}

.advertising-section .minimum-sales-note {
    margin-bottom: 20px;
}

.required-asterisk {
    color: var(--brand-red);
    font-weight: bold;
}

/* Form validation styles */
.error-input {
    border-color: #dc3545 !important;
    background-color: #fff8f8;
}

.error-input:focus {
    box-shadow: 0 0 0 3px rgba(220, 53, 69, 0.1) !important;
}

.error-message {
    color: #dc3545;
    font-size: 0.875rem;
    margin-top: 0.25rem;
    display: block;
}

/* Style radio buttons with errors */
.form-group.has-error .radio-group {
    border: 1px solid #dc3545;
    padding: 10px;
    border-radius: 6px;
    background-color: #fff8f8;
}

/* Ensure error messages are visible on mobile */
@media (max-width: 768px) {
    .event-booking-container {
        padding: clamp(2rem, 6vw, 3rem) clamp(1rem, 5vw, 1.25rem);
    }

    .form-section {
        padding: 20px;
    }

    .time-input {
        flex: 100%;
    }

    .time-input select {
        width: 100%;
        max-width: none;
    }
    
    .time-inputs {
        gap: 15px;
    }

    .radio-group {
        flex-direction: column;
        gap: 15px;
    }

    .error-message {
        font-size: 0.8rem;
        margin-top: 0.5rem;
    }
} 